<template>
  <div
    :style="{
      width: '100%',
      padding: '30px 7% 40px',
      margin: '0px auto',
      position: 'relative',
      background: '#edeff1',
    }"
  >
    <el-button
      :style="{
        border: '0',
        cursor: 'pointer',
        padding: '0 10px',
        margin: '0 10px 20px 0',
        color: '#fff',
        clear: 'both',
        outline: 'none',
        borderRadius: '6px',
        width: 'auto',
        lineHeight: '36px',
        fontSize: '14px',
        height: '36px',
      }"
      type="warning"
      size="mini"
      @click="backClick"
      class="el-icon-back chaxun"
      ></el-button
    >
    <div
      class="section-title"
      :style="{
        padding: '12px 0 0',
        margin: '0px auto',
        color: '#333',
        textAlign: 'center',
        background: 'none',
        width: '100%',
        fontSize: '24px',
        fontWeight: '600',
        height: '60px',
      }"
    >
      
    </div>
    <el-table :data="tableData" style="width: 100%">
      <el-table-column label="" prop="title"> </el-table-column>
      <el-table-column label="" prop="addtime"> </el-table-column>
      <el-table-column label="" width="150">
        <template slot-scope="scope">
          <el-button
            size="mini"
            @click="handleEdit(scope.$index, scope.row)"
            class="xiugaib"
            ></el-button
          >
          <el-button
            size="mini"
            type="danger"
            @click="handleDelete(scope.$index, scope.row)"
            ></el-button
          >
        </template>
      </el-table-column>
    </el-table>

    <el-pagination
      background
      id="pagination"
      class="pagination"
      :pager-count="7"
      :page-size="pageSize"
      :page-sizes="pageSizes"
      prev-text=""
      next-text=""
      :hide-on-single-page="false"
      :layout="['prev', 'pager', 'next'].join()"
      :total="total"
      :style="{
        padding: '25px 0',
        margin: '0 auto',
        color: '#333',
        textAlign: 'left',
        background: 'none',
        width: '100%',
        clear: 'both',
        lineHeight: '40px',
        fontWeight: '500',
        height: 'auto',
        order: '50',
      }"
      @current-change="curChange"
      @prev-click="prevClick"
      @next-click="nextClick"
    ></el-pagination>
  </div>
</template>

<script>
export default {
  data() {
    return {
      layouts: "",
      tableData: [],
      total: 1,
      pageSize: 10,
      pageSizes: [10, 20, 30, 50],
      totalPage: 1,
    };
  },
  created() {
    this.getMyForumList(1);
  },
  methods: {
    backClick() {
      this.$router.push("/index/center");
    },
    getMyForumList(page) {
      this.$http
        .get("forum/page", {
          params: {
            page,
            limit: this.pageSize,
            parentid: 0,
            sort: "istop,toptime",
            order: "desc,desc",
          },
        })
        .then((res) => {
          if (res.data.code == 0) {
            this.tableData = res.data.data.list;
            this.total = res.data.data.total;
            this.pageSize = res.data.data.pageSize;
            this.pageSizes = [
              this.pageSize,
              this.pageSize * 2,
              this.pageSize * 3,
              this.pageSize * 5,
            ];
            this.totalPage = res.data.data.totalPage;
          }
        });
    },
    curChange(page) {
      this.getMyForumList(page);
    },
    prevClick(page) {
      this.getMyForumList(page);
    },
    nextClick(page) {
      this.getMyForumList(page);
    },
    handleEdit(index, row) {
      this.$router.push({ path: "/index/forumAdd", query: { id: row.id } });
    },
    handleDelete(index, row) {
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      }).then(() => {
        let delIds = new Array();
        delIds.push(row.id);
        this.$http.post("forum/delete", delIds).then((res) => {
          if (res.data.code == 0) {
            this.$message({
              type: "success",
              message: "!",
              duration: 1500,
              onClose: () => {
                this.getMyForumList(1);
              },
            });
          }
        });
      });
    },
  },
};
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
