<template>
  <div
    class="container"
    :style="{ margin: '0 200px 20px' }"
    style="padding: 150px 0;"
  >
    <el-alert
      title=""
      type="success"
      :closable="false"
    ></el-alert>
    <!-- <div class="top-content">
      <span></span>
      <el-input style="width:300px" v-model="name" placeholder=""></el-input>
      <span style="margin-left:20px"></span>
      <el-input style="width:300px" v-model="account" placeholder=""></el-input>
    </div> -->
    <!-- <div class="price-content">
      <span></span>
      <span>99.0</span>
    </div> -->
    <div class="pay-type-content">
      <div class="pay-type-item">
        <el-radio v-model="type" label=""></el-radio>
        <img src="@/assets/weixin.png" alt />
        <!-- <span></span> -->
      </div>
      <div class="pay-type-item">
        <el-radio v-model="type" label=""></el-radio>
        <img src="@/assets/zhifubao.png" alt />
        <!-- <span></span> -->
      </div>
      <div class="pay-type-item">
        <el-radio v-model="type" label=""></el-radio>
        <img src="@/assets/jianshe.png" alt />
        <!-- <span></span> -->
      </div>
      <div class="pay-type-item">
        <el-radio v-model="type" label=""></el-radio>
        <img src="@/assets/nongye.png" alt />
        <!-- <span></span> -->
      </div>
      <div class="pay-type-item">
        <el-radio v-model="type" label=""></el-radio>
        <img src="@/assets/zhongguo.png" alt />
        <!-- <span></span> -->
      </div>
      <div class="pay-type-item">
        <el-radio v-model="type" label=""></el-radio>
        <img src="@/assets/jiaotong.png" alt />
        <!-- <span></span> -->
      </div>
    </div>
    <div class="buton-content">
      <el-button @click="submitTap" type="primary"></el-button>
      <el-button @click="back()"></el-button>
    </div>
  </div>
</template>
<script>
// import { Message } from "element-ui";
export default {
  data() {
    return {
      name: "",
      account: "",
      type: "",
      table: "",
      obj: "",
    };
  },
  mounted() {
    let table = localStorage.getItem("paytable");
    let obj = JSON.parse(localStorage.getItem("payObject"));
    this.table = table;
    this.obj = obj;
  },
  methods: {
    submitTap() {
      // if (!this.name) {
      //   this.$message.error("");
      //   return;
      // }
      // if (!this.account) {
      //   this.$message.error("");
      //   return;
      // }
      if (!this.type) {
        this.$message.error("");
        return;
      }
      this.$confirm(`?`, "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      }).then(() => {
        this.obj.ispay = "";
        this.$http.post(`${this.table}/update`, this.obj).then((res) => {
          if (res.data && res.data.code === 0) {
            this.$message({
              message: "",
              type: "success",
              duration: 1500,
              onClose: () => {
                this.$router.go(-1);
              },
            });
          } else {
            this.$message.error(res.data.msg);
          }
        });
      });
    },
    back() {
      this.$router.go(-1);
    },
  },
};
</script>
<style lang="scss" scoped>
.container {
  margin: 10px;
  font-size: 14px;
  span {
    width: 60px;
  }
  .top-content {
    display: flex;
    align-items: center;
    padding: 20px;
  }
  .price-content {
    display: flex;
    align-items: center;
    margin-top: 20px;
    padding-bottom: 20px;
    padding: 20px;
    border-bottom: 1px solid #eeeeee;
    font-size: 20px;
    font-weight: bold;
    color: red;
  }
  .pay-type-content {
    display: flex;
    align-items: center;
    margin-top: 20px;
    flex-wrap: wrap;
    span {
      width: 100px;
    }
    .pay-type-item {
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 300px;
      margin: 20px;
      border: 1px solid #eeeeee;
      padding: 20px;
    }
  }
  .buton-content {
    margin: 20px;
  }
}
</style>
