<template>
  <div
    class="forum-preview"
    :style="{
      width: '100%',
      padding: '30px 7% 40px',
      margin: '0px auto',
      position: 'relative',
      background: '#edeff1',
    }"
  >
    <div
      :style="{
        width: '100%',
        padding: '20px 0',
        margin: '0px auto 0',
        textAlign: 'center',
        background: 'none',
        height: 'auto',
      }"
    >
      <div
        :style="{
          padding: '0 20px 0 0',
          color: '#333',
          textAlign: 'center',
          background: 'none',
          display: 'inline-block',
          width: 'auto',
          fontSize: '24px',
          fontWeight: '600',
        }"
      >
        
      </div>
    </div>
    <el-form
      :style="{
        padding: '0px 0',
        margin: '10px 0 30px',
        alignItems: 'center',
        flexWrap: 'wrap',
        background: 'none',
        display: 'flex',
        width: '100%',
        justifyContent: 'center',
        height: 'auto',
      }"
      :inline="true"
      :model="formSearch"
      class="list-form-pv"
    >
      <el-form-item :style="{ margin: '0 0px' }">
        <el-input v-model="title" placeholder=""></el-input>
      </el-form-item>
      <div :style="{ display: 'flex' }">
        <el-button class="searchBtn chaxun" type="primary" @click="getForumList(1)">
          <span
            class="icon iconfont icon-fangdajing01"
            :style="{ color: '#fff', margin: '0 10px 0 0', fontSize: '16px' }"
          ></span>
          
        </el-button>
        <el-button class="pubBtn chaxun" type="primary" @click="toForumAdd">
          <span
            class="icon iconfont icon-tianjia14"
            :style="{ color: '#fff', margin: '0 10px 0 0', fontSize: '14px' }"
          ></span>
          
        </el-button>
      </div>
    </el-form>
    <div
      class="z-box"
      :style="{
        width: '100%',
        padding: '0px',
        margin: '20px 0 0',
        flexWrap: 'wrap',
        display: 'flex',
      }"
    >
      <div
        class="section-content"
        v-for="item in forumList"
        :key="item.id"
        @click="toForumDetail(item)"
      >
        <div
          :style="{
            overflow: 'hidden',
            whiteSpace: 'nowrap',
            color: '#666',
            background: 'none',
            width: 'calc(100% - 0px)',
            fontSize: '14px',
            lineHeight: '30px',
            textOverflow: 'ellipsis',
            height: '30px',
          }"
          class="item-style"
        >
          {{ item.title }}
        </div>
        <div
          :style="{
            margin: '0 30px 0 0',
            fontSize: '14px',
            color: '#999',
            display: 'none',
          }"
          class="item-style"
        >
          {{ item.username }}
        </div>
        <div
          :style="{ color: '#999', fontSize: '14px', display: 'none' }"
          class="item-style"
        >
          {{ item.addtime }}
        </div>
      </div>
    </div>

    <el-pagination
      background
      id="pagination"
      class="pagination"
      :pager-count="7"
      :page-size="pageSize"
      :page-sizes="pageSizes"
      prev-text=""
      next-text=""
      :hide-on-single-page="false"
      :layout="['prev', 'pager', 'next'].join()"
      :total="total"
      :style="{
        padding: '25px 0',
        margin: '0 auto',
        color: '#333',
        textAlign: 'left',
        background: 'none',
        width: '100%',
        clear: 'both',
        lineHeight: '40px',
        fontWeight: '500',
        height: 'auto',
        order: '50',
      }"
      @current-change="curChange"
      @prev-click="prevClick"
      @next-click="nextClick"
    ></el-pagination>
  </div>
</template>

<script>
export default {
  //
  data() {
    return {
      formSearch: {},
      title: "",
      layouts: "",
      forumList: [],
      total: 1,
      pageSize: 10,
      pageSizes: [10, 20, 30, 50],
      totalPage: 1,
    };
  },
  created() {
    this.getForumList(1);
  },
  //
  methods: {
    getForumList(page) {
      let params = {
        page,
        limit: this.pageSize,
        isdone: "",
        sort: "istop,toptime",
        order: "desc,desc",
      };
      let searchWhere = {};
      if (this.title != "") searchWhere.title = "%" + this.title + "%";
      this.$http
        .get("forum/flist", { params: Object.assign(params, searchWhere) })
        .then((res) => {
          if (res.data.code == 0) {
            this.forumList = res.data.data.list;
            this.total = res.data.data.total;
            this.pageSize = res.data.data.pageSize;
            this.pageSizes = [
              this.pageSize,
              this.pageSize * 2,
              this.pageSize * 3,
              this.pageSize * 5,
            ];
            this.totalPage = res.data.data.totalPage;
          }
        });
    },
    curChange(page) {
      this.getForumList(page);
    },
    prevClick(page) {
      this.getForumList(page);
    },
    nextClick(page) {
      this.getForumList(page);
    },
    toForumAdd() {
      this.$router.push("/index/forumAdd");
    },
    toForumDetail(item) {
      this.$router.push({ path: "/index/forumDetail", query: { id: item.id } });
    },
  },
};
</script>

